@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400;1,500;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Roboto:ital,wght@0,400;0,500;0,700;0,900;1,400;1,500;1,700;1,900&display=swap');
$primary-color: #AA0601;
$secondary-color: #650300;

$gray: #555555;
$lightpink: #AA06010D;
$yellow: #FFE180;
$font-xs: 14px;
$font-s: 16px;
//   16px ==  
$font-m: 20px;
$font-l: 24px;
$font-xl: 36px;
$font-xxl: 48px;
$font-xxxl: 64px;
$font-xxxxl: 72px;
@mixin hide-text {
    overflow: hidden;
    text-indent: 101%;
    white-space: nowrap;
}

@mixin hide-scrollbar {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;

    .TheSelector ::-webkit-scrollbar {
        display: none; //
    }
}

